<template lang="">
    <v-container>
    <h1 class="mb-4">ÏûêÏú†Í≤åÏãúÌåê - ÏÉÅÏÑ∏</h1>
            <v-row>
      <v-col class="m-2 mr-auto">{{ article.writer}}</v-col>
      <v-col class="m-2">
        <span>{{ formatDateSecond(article.createdDate) }}</span>
      </v-col>
      <v-col class="m-2">
        <span>
          {{ formatDateSecond(article.modifiedDate)}}
        </span>
      </v-col>
    </v-row>
    <v-row>
      <v-col class="m-2">{{ `[${article.categoryName}]` }}</v-col>
      <v-col class="m-2 mr-auto text-break">{{ article.title }}</v-col>
      <v-col class="m-2">
        Ï°∞ÌöåÏàò: {{ article.viewCount }}
      </v-col>
    </v-row>
    <hr>
    <v-row class="my-3">
      <v-col class="w-100 border text-break">{{ article.content }}</v-col>
    </v-row>
    <v-row>
      <v-col class="w-100">
        <template v-if="article.isFileExist">
          <template v-for="fileDTO in fileList" :key="fileDTO.fileId">
            <a href="javascript:void(0);" @click="fileDownload(fileDTO.fileId)">
              {{ fileDTO.fileOriginName }}
            </a>
            <br>
          </template>
        </template>
      </v-col>
    </v-row>
    <v-row class="bg-light">
      <v-col>
        <div id="commentList">
          <template v-if="commentList">
            <template v-for="commentDTO in commentList" :key="commentDTO.commentId">
              <div class="row justify-content-start px-2 pt-2">
                {{ formatDateSecond(commentDTO.createdDate) }}
              </div>
              <div class="row border-bottom px-2 pb-3 text-break">
                {{ commentDTO.content }}
              </div>
            </template>
          </template>
        </div>
        <v-form v-model="commentFormValid" @submit.prevent="validateCommentForm">
          <v-row class="py-3">
            <v-col cols="10">
                <v-textarea v-model="commentContent" label="ÎåìÍ∏Ä ÎÇ¥Ïö©" :rules="commentContentRules"></v-textarea>

            </v-col>
            <v-col cols="2">
              <v-btn color="primary" @click="validateCommentForm">
                ÎåìÍ∏Ä Îì±Î°ù
              </v-btn>
            </v-col>
          </v-row>
        </v-form>
      </v-col>
    </v-row>
    <v-row>
        <v-col class="text-center">
            <v-btn color="primary" @click="$router.push('/list')">Î™©Î°ù</v-btn>
            <v-btn color="" @click="$router.push(`/modify/${articleId}`)">ÏàòÏ†ï</v-btn>
            <v-btn color="" @click="isDeleteModalShow = true">ÏÇ≠Ï†ú</v-btn>
        </v-col>
    </v-row>
    <v-dialog
      v-model="isDeleteModalShow"
      width="1024"
      persistent
    >
      <v-card>
        <v-form v-model="deleteFormValid" @submit.prevent="deleteArticle">
        <v-card-title>
            <span class="text-h5">Í≤åÏãúÎ¨º ÏÇ≠Ï†ú</span>
        </v-card-title>
        <v-card-text>
                <v-text-field
                  label="Password*"
                  type="password"
                    v-model="password"
                  :rules="passwordRules"
                  required
                ></v-text-field>
        </v-card-text>
        <v-card-actions>
            
          <v-spacer></v-spacer>
          <v-btn
            color="blue-darken-1"
            variant="text"
            @click="isDeleteModalShow = false"
          >
            Ï∑®ÏÜå
          </v-btn>
          <v-btn
            color="blue-darken-1"
            variant="text"
            @click="deleteArticle"
          >
            ÏÇ≠Ï†ú
          </v-btn>
        </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    </v-container>
    
</template>
<script>
import { ref, computed, onBeforeMount, inject } from "vue";
import { useStore } from "vuex";
import { useRouter, useRoute } from "vue-router"
import { formatDateSecond } from "../assets/common";

export default {
    name: "ArticleDetail",
    setup() {
        // Í≥µÌÜµ ÏöîÏÜå
        const axios = inject("axios");
        const route = useRoute();
        const router = useRouter();

        // article Data Íµ¨ÏÑ±ÏöîÏÜå
        const { articleId } = route.params
        const article = ref({
            writer: "",
            title: "",
            content: "",
            categoryName: "",
            viewCount: 0,
            isFileExist: false,
            createdDate: "",
            modifiedDate: "",
        });

        // article Data Í∞ÄÏ†∏Ïò§Í∏∞
        const getArticle = async () => {
            try {
                const response = await axios.get(`/v1/articles/${articleId}`, {});
                article.value = response.data;

            } catch (error) {
                console.error(error);
            }
        }
        onBeforeMount(() => {
            getArticle();
        });


        // comment Data Í∞ÄÏ†∏Ïò§Í∏∞
        const commentList = ref([]);
        const getCommentList = async () => {
            try {
                const response = await axios.get(`/v1/articles/${articleId}/comments`, {});
                commentList.value = {
                    ...response.data,
                }

            } catch (error) {
                console.error(error);
            }
        }
        onBeforeMount(() => {
            getCommentList();
        });

        // comment Îì±Î°ù
        const commentFormValid = ref(false);
        const commentContent = ref("");
        const commentContentRules = [
            (value) => !!value || "ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
            (value) => /^.{1,255}$/g.test(value) || "ÎÇ¥Ïö©ÏùÄ 1~255ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
        ];

        // comment Îì±Î°ùÏãú validation Ï≤¥ÌÅ¨ ÌõÑ Îì±Î°ù
        const validateCommentForm = async () => {
            if (!commentFormValid.value) {
                return;
            }
            try {
                const response = await axios.post(`/v1/articles/${articleId}/comments`, {
                    content: commentContent.value,
                });
                // ÏÑ±Í≥µÌïú Í≤ΩÏö∞ÏóêÎßå ÎåìÍ∏Ä Î™©Î°ù Í∞±Ïã†
                if (response.status === 201 || response.status === 200) {
                    getCommentList();
                }
            } catch (error) {
                console.error(error);
            }
        }


        // file Data Í∞ÄÏ†∏Ïò§Í∏∞
        const fileList = ref([]);
        const getFileList = async () => {
            try {
                const response = await axios.get(`/v1/articles/${articleId}/files`, {});
                fileList.value = response.data;

            } catch (error) {
                console.error(error);
            }
        }
        onBeforeMount(() => {
            getFileList();
        });

        // file download
        const fileDownload = async (fileId) => {
            try {
                // TODO: spring ServerÏóê Range Íµ¨ÌòÑÏùÄ ÌñàÏßÄÎßå ÏÇ¨Ïö©ÏùÄ Î≥¥Î•ò(ÌÖåÏä§Ìä∏ÎèÑ ÏïàÌï¥Î¥Ñ)
                const response = await axios.get(`/v1/articles/${articleId}/files/${fileId}`, {
                    responseType: "blob",
                    headers: {
                        "Accept": "application/octet-stream"
                    }
                });

                const url = window.URL.createObjectURL(new Blob([response.data]));
                const link = document.createElement("a");
                link.href = url;
                link.setAttribute("download", response.headers["content-disposition"].split("filename=")[1]);
                document.body.appendChild(link);
                link.click();
            } catch (error) {
                console.error(error);
            }
        }

        // delete modal ÏÑ§Ï†ï
        const isDeleteModalShow = ref(false);

        // delete Í¥ÄÎ†® ÏöîÏÜå ÏÑ§Ï†ï
        const deleteFormValid = ref(false);
        const password = ref(null);
        const passwordRules = [
            (value) => !!value || "ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
            (value) =>
                /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{4,15}$/.test(
                    value
                ) || "ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÏòÅÎ¨∏ ÎåÄÏÜåÎ¨∏Ïûê, Ïà´Ïûê, ÌäπÏàòÎ¨∏Ïûê(!@#$%^&*)Î•º Ï°∞Ìï©Ìïú 4~15ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];

        // article delete
        const deleteArticle = async () => {
            if (!deleteFormValid.value) {
                return;
            }
            try {
                const response = await axios.delete(`/v1/articles/${articleId}`, {
                    data: {
                        articleId,
                        password: password.value,
                    }
                });
                if (response.status === 200 || response.status === 204) {
                    router.push("/list")
                }
            } catch (error) {
                // TODO: ÏóêÎü¨Ï≤òÎ¶¨ Ï¢ÄÎçî Ïö∞ÏïÑÌïòÍ≤å Í≥†ÎØºÌïòÍ∏∞
                if (error.response.data.code === "ARTICLE.ERR.400.DTO.PASSWORD") {
                    alert("ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.");
                }
                console.error(error);
            }
        }


        return {
            article,
            articleId,
            commentList,
            commentFormValid,
            commentContent,
            commentContentRules,
            validateCommentForm,
            formatDateSecond,
            fileList,
            fileDownload,
            isDeleteModalShow,
            deleteFormValid,
            password,
            passwordRules,
            deleteArticle,
        }
    }

}
</script>
<style lang="">
    
</style>