<template>
  <v-container fluid>
    <h1>ÏûêÏú†Í≤åÏãúÌåê - Îì±Î°ù</h1>
    <v-form v-model="formValid" @submit.prevent="submitForm">

      <v-card>
        <v-card-title>Ïπ¥ÌÖåÍ≥†Î¶¨ *</v-card-title>
        <v-card-text>
          <v-select v-model="categoryId" :items="categories" item-title="name" item-value="categoryId" single-line
            :rules="categoryIdRules" label="Ïπ¥ÌÖåÍ≥†Î¶¨"></v-select>

        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÏûëÏÑ±Ïûê *</v-card-title>
        <v-card-text>
          <v-text-field v-model="writer" label="ÏûëÏÑ±Ïûê" :rules="writerRules"></v-text-field>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÎπÑÎ∞ÄÎ≤àÌò∏ *</v-card-title>
        <v-card-text>
          <v-text-field v-model="password" label="ÎπÑÎ∞ÄÎ≤àÌò∏" type="password" :rules="passwordRules"></v-text-field>
          <v-text-field v-model="passwordCheck" label="ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏" type="password"
            :rules="passwordCheckRules"></v-text-field>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>Ï†úÎ™© *</v-card-title>
        <v-card-text>
          <v-text-field v-model="title" label="Ï†úÎ™©" :rules="titleRules"></v-text-field>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÎÇ¥Ïö© *</v-card-title>
        <v-card-text>
          <v-textarea v-model="content" label="ÎÇ¥Ïö©" :rules="contentRules"></v-textarea>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÌååÏùºÏ≤®Î∂Ä</v-card-title>
        <v-card-text>
          <v-file-input multiple v-model="files" label="ÌååÏùº Ï∞æÍ∏∞" id="customFileInput" name="file1"></v-file-input>
        </v-card-text>
      </v-card>

      <div class="d-flex justify-space-between">
        <v-btn class="float-left" @click="$router.push('/list')">Ï∑®ÏÜå</v-btn>
        <v-btn color="primary" class="float-right" @click="submitForm">Ï†ÄÏû•</v-btn>
      </div>

    </v-form>
  </v-container>
</template>
  
<script>
import { ref, onBeforeMount, inject } from "vue";
import { useStore } from "vuex";
import { useRouter, useRoute } from "vue-router"

export default {
  name: "ArticleWrite",

  setup() {
    const axios = inject("axios");
    const store = useStore();
    const router = useRouter();


    // Ïπ¥ÌÖåÍ≥†Î¶¨ Î™©Î°ù Ï°∞Ìöå
    // TODO: ListÎûë Ï§ëÎ≥µÎêòÍ≥† ÌïòÎäîÎç∞, Í≥µÌÜµ Î™®ÎìàÎ°ú Îî∞Î°ú ÎπºÎäî Î∞©Ïïà Í≥†Î†§ ÌïÑÏöî
    // 
    const categories = ref([]);
    const getCategories = async () => {
      try {
        const response = await axios.get("/v1/categories", {});

        categories.value = response.data.map((categoryData) => ({
          categoryId: categoryData.categoryId,
          name: categoryData.name,
        }))
      } catch (error) {
        console.error(error);
      }
    };
    onBeforeMount(() => {
      getCategories();
    });


    // formÏóê Îß§ÌïëÎê† refÎì§Í≥º Ìï¥Îãπ refÎì§Ïóê validationÏùÑ ÏúÑÌïú rules
    // TODO: ÏÉùÍ∞ÅÌï¥Î≥¥Îãà Í∞ùÏ≤¥ ÌïòÎÇòÎ°ú Î¨∂Ïñ¥ÏÑú Í¥ÄÎ¶¨ÌïòÎäîÍ≤å Îçî Ï¢ãÏùÑ Í≤É Í∞ôÎã§.
    // TODO: Î∞òÎ≥µÎêòÎäî ÏöîÏÜåÎãà ÎÇòÏ§ëÏóê Í≥µÌÜµÏúºÎ°ú ÎπºÍ∏∞
    const formValid = ref(false);

    const categoryId = ref(null);
    const categoryIdRules = [(value) => !!value || "Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî"];

    const writer = ref(null);
    const writerRules = [
      (value) => !!value || "ÏûëÏÑ±ÏûêÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
      (value) => /^.{3,4}$/g.test(value) || "ÏûëÏÑ±ÏûêÎäî 3~4ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
    const password = ref(null);
    const passwordRules = [
      (value) => !!value || "ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
      (value) =>
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{4,15}$/.test(
          value
        ) || "ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÏòÅÎ¨∏ ÎåÄÏÜåÎ¨∏Ïûê, Ïà´Ïûê, ÌäπÏàòÎ¨∏Ïûê(!@#$%^&*)Î•º Ï°∞Ìï©Ìïú 4~15ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
    const passwordCheck = ref(null);
    const passwordCheckRules = [
      (value) => !!value || "ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
      (v) => v === password.value || "ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§",];
    const title = ref(null);
    const titleRules = [
      (value) => !!value || "Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
      (value) => /^.{4,99}$/g.test(value) || "Ï†úÎ™©ÏùÄ 4~99ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
    const content = ref(null);
    const contentRules = [
      (value) => !!value || "ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
      (value) => /^.{4,1999}$/g.test(value) || "ÎÇ¥Ïö©ÏùÄ 4~1999ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
    const files = ref([]);

    // form submit Ï≤òÎ¶¨
    const submitForm = () => {
      // form validation Ï≤¥ÌÅ¨
      if (formValid.value) {
        const formData = new FormData();
        formData.append("articleDTO", JSON.stringify({
          categoryId: categoryId.value,
          writer: writer.value,
          password: password.value,
          title: title.value,
          content: content.value,
        }));
        for (let i = 0; i < files.value.length; i += 1) {
          formData.append("files", files.value[i]);
        }
        axios.post("/v1/articles", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }).then(response => {
          // ÏÑ±Í≥µ ÌñàÏúºÎ©¥ listÎ°ú Í∑ÄÌôò
          if (response.status === 201 || response.status === 200) {
            // Í∞ÄÍ∏∞Ï†ÑÏóê Í≤ÄÏÉâÏ°∞Í±¥ Ï¥àÍ∏∞ÌôîÌï¥ÏÑú Î∞©Í∏à insertÌïú Îç∞Ïù¥ÌÑ∞Í∞Ä Î≥¥Ïù¥ÎèÑÎ°ù
            store.commit("updateSearchParams", {
              categoryId: null,
              startDate: null,
              endDate: null,
              searchWord: null,
              articlePerPage: 10,
              pageNum: 1,
            });
            router.push("/list")
          }

        }).catch(error => {
          console.error(error);
        });
      } else {
        // TODO: form validation Ïã§Ìå® Ïãú Ï≤òÎ¶¨(alertÎßêÍ≥† Îã§Î•∏ Î∞©Î≤ïÏúºÎ°ú)
        // Rules Í∞ÄÏ†∏Îã§ Ïì∞ÎãàÍπê Ïñ¥Îñ§ ÏöîÏÜåÏã§Ìå®ÌñàÎäîÏßÄ focusÍ∞Ä ÏïàÍ∞ÄÍ≥†ÏûàÏùå
        alert("form validation Ïã§Ìå®");
      }
    };




    return {
      categories,
      getCategories,
      formValid,
      categoryId,
      categoryIdRules,
      writer,
      writerRules,
      password,
      passwordRules,
      passwordCheck,
      passwordCheckRules,
      title,
      titleRules,
      content,
      contentRules,
      files,
      submitForm,
    };
  }

};
</script>