<template lang="">

<v-container fluid>
    <h1>ÏûêÏú†Í≤åÏãúÌåê - ÏàòÏ†ï</h1>
    <v-form v-model="formValid" @submit.prevent="submitForm">

      <v-card>
        <v-card-title>Ïπ¥ÌÖåÍ≥†Î¶¨</v-card-title>
        <v-card-text>
            {{ article.categoryName}}

        </v-card-text>
      </v-card>
      <v-card>
        <v-card-title>Îì±Î°ù ÏùºÏãú</v-card-title>
        <v-card-text>
            {{ formatDateSecond(article.createdDate) }}
        </v-card-text>
      </v-card>
      
      <v-card>
        <v-card-title>ÏàòÏ†ï ÏùºÏãú</v-card-title>
        <v-card-text>
            {{ formatDateSecond(article.modifiedDate)}}
        </v-card-text>
      </v-card>
      <v-card>
        <v-card-title>Ï°∞ÌöåÏàò</v-card-title>
        <v-card-text>
            {{ article.viewCount }}
        </v-card-text>
      </v-card>


      <v-card>
        <v-card-title>ÏûëÏÑ±Ïûê *</v-card-title>
        <v-card-text>
            <v-text-field v-model="writer" label="ÏûëÏÑ±Ïûê" :rules="writerRules"></v-text-field>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÎπÑÎ∞ÄÎ≤àÌò∏ *</v-card-title>
        <v-card-text>
          <v-text-field v-model="password" label="ÎπÑÎ∞ÄÎ≤àÌò∏" type="password" :rules="passwordRules"></v-text-field>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>Ï†úÎ™© *</v-card-title>
        <v-card-text>
          <v-text-field v-model="title" label="Ï†úÎ™©" :rules="titleRules"></v-text-field>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÎÇ¥Ïö© *</v-card-title>
        <v-card-text>
          <v-textarea v-model="content" label="ÎÇ¥Ïö©" :rules="contentRules"></v-textarea>
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÌååÏùº Î¶¨Ïä§Ìä∏</v-card-title>
        <v-card-text>
            
        <template v-if="article.isFileExist">
          <v-row v-for="fileDTO in fileList" :key="fileDTO.fileId">
            {{ fileDTO.fileOriginName }}
            <v-btn @click="fileDownload(fileDTO.fileId)">Download</v-btn>
            <v-btn @click="fileDelete(fileDTO.fileId)">X</v-btn>
          </v-row>
        </template>
          
        </v-card-text>
      </v-card>

      <v-card>
        <v-card-title>ÌååÏùºÏ≤®Î∂Ä</v-card-title>
        <v-card-text>
          <v-file-input multiple v-model="files" label="ÌååÏùº Ï∞æÍ∏∞" id="customFileInput" name="file1"></v-file-input>
        </v-card-text>
      </v-card>

      <div class="d-flex justify-space-between">
        <v-btn class="float-left" @click="$router.push(`/view/${articleId}`)">Ï∑®ÏÜå</v-btn>
        <v-btn color="primary" class="float-right" @click="submitForm">Ï†ÄÏû•</v-btn>
      </div>

    </v-form>
  </v-container>
</template>
<script>
import { ref, onBeforeMount, inject } from "vue";
import { useStore } from "vuex";
import { useRouter, useRoute } from "vue-router"
import { formatDateSecond } from "../assets/common";

export default {
    name: "ArticleModify",

    setup() {
        const axios = inject("axios");
        const store = useStore();
        const router = useRouter();
        const route = useRoute();


        // article Data Íµ¨ÏÑ±ÏöîÏÜå
        const { articleId } = route.params
        const article = ref({
            writer: "",
            title: "",
            content: "",
            categoryName: "",
            viewCount: 0,
            isFileExist: false,
            createdDate: "",
            modifiedDate: "",
        });

        // article Data Í∞ÄÏ†∏Ïò§Í∏∞
        const getArticle = async () => {
            try {
                const response = await axios.get(`/v1/articles/${articleId}`, {});
                article.value = response.data;
            } catch (error) {
                console.error(error);
            }
        }
        onBeforeMount(() => {
            getArticle();
        });

        // file Data Í∞ÄÏ†∏Ïò§Í∏∞
        const fileList = ref([]);
        const getFileList = async () => {
            try {
                const response = await axios.get(`/v1/articles/${articleId}/files`, {});
                fileList.value = response.data;

            } catch (error) {
                console.error(error);
            }
        }
        onBeforeMount(() => {
            getFileList();
        });

        // file download
        const fileDownload = async (fileId) => {
            try {
                // TODO: spring ServerÏóê Range Íµ¨ÌòÑÏùÄ ÌñàÏßÄÎßå ÏÇ¨Ïö©ÏùÄ Î≥¥Î•ò(ÌÖåÏä§Ìä∏ÎèÑ ÏïàÌï¥Î¥Ñ)
                const response = await axios.get(`/v1/articles/${articleId}/files/${fileId}`, {
                    responseType: "blob",
                    headers: {
                        "Accept": "application/octet-stream"
                    }
                });

                const url = window.URL.createObjectURL(new Blob([response.data]));
                const link = document.createElement("a");
                link.href = url;
                link.setAttribute("download", response.headers["content-disposition"].split("filename=")[1]);
                document.body.appendChild(link);
                link.click();
            } catch (error) {
                console.error(error);
            }
        }

        // file delete
        const deleteFiles = ref([])
        const fileDelete = (fileId) => {
            deleteFiles.value.push(fileId);
            fileList.value = fileList.value.filter(file => file.fileId !== fileId);
        }

        // form Í¥ÄÎ†® ÏöîÏÜå

        const formValid = ref(false);

        const writer = ref(null);
        const writerRules = [
            (value) => !!value || "ÏûëÏÑ±ÏûêÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
            (value) => /^.{3,4}$/g.test(value) || "ÏûëÏÑ±ÏûêÎäî 3~4ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
        const password = ref(null);
        const passwordRules = [
            (value) => !!value || "ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
            (value) =>
                /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{4,15}$/.test(
                    value
                ) || "ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÏòÅÎ¨∏ ÎåÄÏÜåÎ¨∏Ïûê, Ïà´Ïûê, ÌäπÏàòÎ¨∏Ïûê(!@#$%^&*)Î•º Ï°∞Ìï©Ìïú 4~15ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
        const title = ref(null);
        const titleRules = [
            (value) => !!value || "Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
            (value) => /^.{4,99}$/g.test(value) || "Ï†úÎ™©ÏùÄ 4~99ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
        const content = ref(null);
        const contentRules = [
            (value) => !!value || "ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",
            (value) => /^.{4,1999}$/g.test(value) || "ÎÇ¥Ïö©ÏùÄ 4~1999ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî",];
        const files = ref([]);

        // form submit Ï≤òÎ¶¨

        const submitForm = () => {
            // form validation Ï≤¥ÌÅ¨
            if (formValid.value) {
                const formData = new FormData();
                formData.append("articleDTO", JSON.stringify({
                    writer: writer.value,
                    password: password.value,
                    title: title.value,
                    content: content.value,
                }));
                for (let i = 0; i < deleteFiles.value.length; i += 1) {
                    formData.append("deleteFiles", deleteFiles.value[i]);
                }

                for (let i = 0; i < files.value.length; i += 1) {
                    formData.append("files", files.value[i]);
                }

                axios.put(`/v1/articles/${articleId}`, formData, {
                    headers: {
                        "Content-Type": "multipart/form-data"
                    }
                }).then(response => {
                    console.log(response);
                    // ÏÑ±Í≥µ ÌñàÏúºÎ©¥ viewÎ°ú Í∑ÄÌôò
                    if (response.status === 204 || response.status === 200) {
                        router.push(`/view/${articleId}`);
                    }

                }).catch(error => {
                    console.error(error);
                });
            } else {
                alert("form validation Ïã§Ìå®");
            }
        };

        return {
            articleId,
            article,
            formValid,
            writer,
            writerRules,
            password,
            passwordRules,
            title,
            fileList,
            titleRules,
            content,
            contentRules,
            files,
            submitForm,
            formatDateSecond,
            fileDownload,
            fileDelete,
            deleteFiles,
        };
    }
}
</script>
<style lang="">
    
</style>